<template>
  <div class="row q-col-gutter-md">
    <div class="col-12 col-sm-6" v-for="opt in selection" :key="opt">
      <q-radio
        :dense="dense"
        v-model="modelValue"
        :val="JSON.stringify(opt)"
        :label="opt.label"
        hide-bottom-space
        :name="name || undefined"
        :color="opt.color || 'secondary'"
        class="field-select text-body1 font-medium rounded bg-none text-primary-500 q-py-sm q-pl-sm flex-label full-width full-height"
        :rules="[(val) => (required ? !!val || '' : true)]"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'RadioSelect',

  emits: ['updated'],

  props: {
    initModel: {},
    selection: {
      type: Array,
      default: () => [],
    },
    name: {
      type: String,
      default: '',
    },
    required: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      modelValue: '',
    };
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.formatModel(val);
      },
    },
    modelValue: {
      async handler(val) {
        let model = null;
        try {
          model = JSON.parse(val);
        } catch (e) {
          model = val;
        }
        this.$emit('updated', model);
      },
    },
  },

  mounted() {
    if (this.initModel) this.formatModel(this.initModel);
  },

  methods: {
    formatModel(val) {
      if (typeof val === 'object') {
        this.modelValue = JSON.stringify(val);
      } else {
        this.modelValue = val;
      }
    },
  },
});
</script>
